.LabeledList {
  display: table;
  border-collapse: collapse;
  border-spacing: 0;
  margin: -3px -6px;
  margin-bottom: 0;
  padding: 0;
}

.LabeledList__row {
  display: table-row;
}

.LabeledList__row:last-child .LabeledList__cell {
  padding-bottom: 0;
}

.LabeledList__cell {
  display: table-cell;
  margin: 0;
  padding: 3px 6px;
  border: 0;
  text-align: left;
  vertical-align: baseline;
}

.LabeledList__label {
  width: 1%;
  white-space: nowrap;
  min-width: 60px;
}

.LabeledList__buttons {
  width: 0.1%;
  white-space: nowrap;
  text-align: right;
  padding-top: 1px;
  padding-bottom: 0;
}

.Layout__titleBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 32px;
}

.Layout__content {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-x: hidden;
  scrollbar-base-color: #1b1b1b;
  scrollbar-face-color: #515151;
  scrollbar-3dlight-color: #6c6c6c;
  scrollbar-highlight-color: #6c6c6c;
  scrollbar-track-color: #1b1b1b;
  scrollbar-arrow-color: #878787;
  scrollbar-shadow-color: #363636;
}

.Layout__content--scrollable {
  overflow-y: scroll;
}

.Layout__dimmer {
  position: fixed;
  top: 32px;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(70, 70, 70, 0.25);
}

.Layout__toast {
