.theme-clockwork .Tooltip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.theme-clockwork .Tooltip::after {
  position: absolute;
  display: block;
  z-index: 2;
  width: 250px;
  padding: 10px;
  transform: translateX(-50%);
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  white-space: normal;
  text-align: left;
  content: attr(data-tooltip);
  transition: all 150ms;
  border: 1px solid #170800;
  background-color: #2D1400;
}
.theme-clockwork .Tooltip:hover::after {
  pointer-events: none;
  visibility: visible;
  opacity: 1;
}
.theme-clockwork .Tooltip--top::after {
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(8px);
}
.theme-clockwork .Tooltip--top:hover::after {
  transform: translateX(-50%) translateY(-8px);
}
.theme-clockwork .Tooltip--bottom::after {
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(-8px);
}
.theme-clockwork .Tooltip--bottom:hover::after {
  transform: translateX(-50%) translateY(8px);
}
.theme-clockwork .Tooltip--left::after {
  top: 50%;
  right: 100%;
  transform: translateX(8px) translateY(-50%);
}
.theme-clockwork .Tooltip--left:hover::after {
  transform: translateX(-8px) translateY(-50%);
}
.theme-clockwork .Tooltip--right::after {
  top: 50%;
  left: 100%;
  transform: translateX(-8px) translateY(-50%);
}
.theme-clockwork .Tooltip--right:hover::after {
  transform: translateX(8px) translateY(-50%);
}